"use client";

interface ModalRoutineProps {
    open: boolean,
    close: () => void
}

export default function ModalRoutine({ open, close }: ModalRoutineProps) {
  if (!open) return null;

  return (
    <div className="fixed inset-0 bg-black bg-opacity-40 flex justify-center items-center z-50">
      <div className="bg-white rounded-2xl shadow-lg p-8 w-96">
        <h2 className="text-xl font-bold mb-4">Cadastro de Rotina</h2>

        <form className="flex flex-col gap-3">
          <input
            type="text"
            placeholder="Nome da rotina"
            className="border rounded-lg p-2"
          />
          <input type="time" className="border rounded-lg p-2" />
          <textarea
            placeholder="Descrição"
            className="border rounded-lg p-2"
          ></textarea>

          <div className="flex justify-end gap-2 mt-4">
            <button
              type="button"
              onClick={close}
              className="px-4 py-2 bg-gray-300 rounded-lg hover:bg-gray-400"
            >
              Cancelar
            </button>
            <button
              type="submit"
              className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700"
            >
              Salvar
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}
